import React, {useState} from "react";
import styles from "../Paginator/Paginator.module.css";
import cn from 'classnames';

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize + 1;

    return <div className={styles.paginator}>
        {portionNumber > 1 ?
            <button className={styles.prevBut} onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>PREV</button> :
            <button className={styles.prevBut} disabled>PREV</button>}

        {pages
            .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map((p) => {
                return <div className={cn({
                    [styles.selectedPage]: currentPage === p
                }, styles.pageNumber)}
                            key={p}
                            onClick={(e) => {
                                onPageChanged(p);
                            }}>{p}</div>
            })}
        {portionCount > portionNumber ?
            <button className={styles.nextBut} onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>NEXT</button> :
            <button className={styles.nextBut} disabled>NEXT</button>}
    </div>
}

export default Paginator;