import React from "react";
import {
    follow,
    setCurrentPage,
    unfollow,
    toggleIsFollowingInProgress, getUsers
} from "../../Redux/Users-reducer";
import {connect} from "react-redux";
import Users from "./Users";
import Preloader from '../../assets/preloader/Preloader';
import {
    getCurrentPage,
    getFollowingInProgress,
    getIsFetching,
    getPageSize,
    getTotalUsersCount,
    getUsersData
} from "../../Redux/Users-selector";
import Paginator from "../../common/Paginator/Paginator";
import cl from "./Users.module.css";

class UsersContainer2 extends React.Component {

    componentDidMount() {
        const {currentPage, pageSize} = this.props;
        this.props.getUsers(currentPage, pageSize);
    }

    onPageChanged = (pageNumber) => {
        const {pageSize} = this.props;
        this.props.getUsers(pageNumber, pageSize)
    }

    render() {
        return <>
            <Paginator currentPage={this.props.currentPage} onPageChanged={this.onPageChanged}
                       totalItemsCount={this.props.totalUsersCount} pageSize={this.props.pageSize}/>
            {this.props.isFetching ?
            <div className={cl.preloaderUsers}><Preloader/></div>
            : null}
            {this.props.isFetching ?
                null
                : <Users users={this.props.users}
                   unfollow={this.props.unfollow}
                   follow={this.props.follow}
                   toggleIsFollowingInProgress={this.props.toggleIsFollowingInProgress}
                   followingInProgress={this.props.followingInProgress}
            />}
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsersData(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

/*let mapDispatchToProps = (dispatch) => {
    return {
        toggleIsFetching: (isFetching) => {
            dispatch(toggleIsFetchingAC(isFetching));
        }
    }
}*/

const UsersContainer = connect(mapStateToProps,
    {
        follow, unfollow, setCurrentPage,
        toggleIsFollowingInProgress, getUsers
    })(UsersContainer2);

export default UsersContainer;

